.page__container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal__box {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    min-height: 170px;
    background-color: rgb(46, 52, 125);
    color: white;
    z-index: 10;
    border-radius: 16px;
    box-shadow: 0px 20px 20px 0px rgba(0, 0, 0, 0.5);
    padding: 30px;
    border: 4px solid rgba(62, 68, 156, 0.7);
}

@media screen and (max-width:800px) {
    .modal__box {
        width: 80%;
        min-height: 170px;
    }
  }

.modal__header {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.modal__title {
    font-size: 25px;
    font-weight: 600;
    letter-spacing: 1.2px;
}

.modal__inputsBox {
    margin-top: 30px;
    margin-bottom: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
}

.modal__input {
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid rgb(62, 68, 156);
    font-size: 18px;
    font-weight: 800;
    letter-spacing: 1.2px;
    color: white;
    align-self: flex-start;
}

::placeholder {
    color: white;
    font-weight: 100;
    font-size: 15px;
    opacity: 0.5;
  }

.modal__submitBtn {
    width: 60%;
    height: 50px;
    border: none;
    border-radius: 10px;
    background-color: rgb(245,244,79);
    color: rgb(46, 52, 125);
    font-weight: 900;
    font-size: 20px;
    letter-spacing: 1.2px;
    transition: 0.2s linear;
    opacity: 1;
}

.modal__submitBtn:hover {
    transition: 0.2s linear;
    opacity: 0.7;
    cursor: pointer;
}

